import { user,userUpdate } from '@/api/user'
const Cookie = process.client ? require('js-cookie'):undefined
export default {
  middleware: 'authenticated', // 也可以是[]
  name: 'SettingsIndex',
  data(){
    return {
      data: {
        email: '',
        bio: '',
        image: '',
        username: '',
        password: ''
      },
    }
  },
  methods: {
    async getUser(){
      const { data } = await user()
      // console.log(data)
      this.data = data.user
    },
    async updateUser(){
      const {data} = await userUpdate(this.data)
      console.log(data)
      if (data.user){
        this.$router.push({
          name: 'profile',
          params: {username:data.user.username}
        })
      }
      
    },
    logout() {
      console.log('user')
      Cookie.remove('user', { path: '' })
      // this.$router.push('/')
    }
  },
  mounted () {
    this.getUser()
  }
}
